/* Beginning of loading chart element styling*/
.chart-loading{
    color: orange;
    width: 100%;
    height: 30px;
    transition: cubic-bezier(1, 0, 0, 1);
    -webkit-animation-name: chart;
            animation-name: chart;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}
@-webkit-keyframes chart{
    0%{color:rgb(5, 231, 5);}
    25%{color: blue;}
    50%{color: blueviolet;}
    75%{color: red;}
    100%{color: orange;}
}
@keyframes chart{
    0%{color:rgb(5, 231, 5);}
    25%{color: blue;}
    50%{color: blueviolet;}
    75%{color: red;}
    100%{color: orange;}
}
/*Ending of loading chart element styling */
/*Beginning of blank feed styling */
.blank-feed{
