    position: relative;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-name: feed;
            animation-name: feed;
    opacity: 0.2;
}

@-webkit-keyframes feed{
    0%{background-color:lightslategray;}
    25%{background-color: lightgray;}
    50%{background-color: lightslategray;}
    75%{background-color: gray;}
    100%{background-color: rgb(175, 170, 170);}
}

@keyframes feed{
    0%{background-color:lightslategray;}
    25%{background-color: lightgray;}
    50%{background-color: lightslategray;}
    75%{background-color: gray;}
    100%{background-color: rgb(175, 170, 170);}
}
